'use client'
import { useState } from 'react'
import { useAccount, useConnect, useContractRead, useContractWrite, useDisconnect } from 'wagmi'
import { injected } from '@wagmi/connectors'
import { parseAbi, createPublicClient, createWalletClient, http } from 'viem'
import { sepolia } from 'viem/chains'

const contractAddress = '0x8D21A5F67eB60aB36C0281a2E8F3F8894aB18D24'
const contractAbi = parseAbi([
  'function balanceOf(address owner) view returns (uint256)',
  'function mintSessionNFT(address to) public'
])

export default function Page() {
  const [status, setStatus] = useState('')
  const { connectAsync } = useConnect({ connector: injected() })
  const { address, isConnected } = useAccount()
  const { disconnect } = useDisconnect()
  const { data: balance } = useContractRead({
    address: contractAddress,
    abi: contractAbi,
    functionName: 'balanceOf',
    args: address ? [address] : undefined,
    watch: true
  })
  const { writeAsync: mint } = useContractWrite({
    address: contractAddress,
    abi: contractAbi,
    functionName: 'mintSessionNFT'
  })

  async function handleConnect() {
    try {
      await connectAsync({ connector: injected() })
      setStatus('Wallet connected')
    } catch {
      setStatus('Connection failed')
    }
  }

  async function handleMint() {
    if (!address) return
    try {
      await mint({ args: [address] })
      setStatus('Session NFT minted successfully!')
    } catch {
      setStatus('Transaction failed')
    }
  }

  return (
    <main className="flex flex-col items-center justify-center min-h-screen bg-gray-50 text-gray-900">
      <h1 className="text-3xl font-bold mb-4">HypnoNeuro Dashboard</h1>
      {!isConnected ? (
        <button onClick={handleConnect} className="px-6 py-3 bg-indigo-600 text-white rounded-lg">Connect MetaMask</button>
      ) : (
        <>
          <p className="mb-2">Connected: {address}</p>
          <button onClick={disconnect} className="mb-4 px-4 py-2 bg-gray-300 rounded">Disconnect</button>
          <p className="mb-4">Current NFT Sessions: {balance?.toString() || 0}</p>
          <button onClick={handleMint} className="px-6 py-3 bg-green-600 text-white rounded-lg">Complete Session (Mint)</button>
          <p className="mt-4 text-sm text-gray-700">{status}</p>
        </>
      )}
    </main>
  )
}
